.soundContainer {
  position: absolute;
  top: 220px;
  right: 70px;
  .soundBlock {
    width: 50px;
    height: 50px;
    border: 1px solid #6db2ff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 2px 10px rgba($color: #6db2ff, $alpha: 0.6);
    cursor: pointer;
    transition: transform 0.15s ease-in-out;
    img {
      width: 30px;
      height: 30px;
    }
  }
  .soundBlock:hover {
    transform: scale(110%);
  }
}
